.graph{padding:10px 0;}
.item{
  display:block;
  position:relative;
  width:100%;
/*   background:#white; */
  text-decoration:none;
  margin: 0 auto;
  left:0;
  overflow:hidden;
  transition: all .5s ease;
}
.item:hover{
  cursor:pointer;
  width:105%;
  left:-2.5%;
  transition: all .25s ease;
}
.item div{
  display: flex;
  justify-content:center;
  align-content:center;
  flex-direction:column;
  width:0;  height:50px;
  color:#fff;
  margin: 0 0;
  padding-right:6px;
  text-align:right;
  font-size:1.2em;
  font-weight: bolder;
  transition:all .5s ease;
}
.item .min{
  position:absolute;
  top:0;  left:0;  bottom:0;  right:0;
}
.item .max:hover + .min{
  left:-30px;
}
.label{
  align-items: center;
}